import { initializeApp } from "firebase/app";
import { getDatabase, ref, push, set, get } from "firebase/database";
import { getAuth, signInAnonymously } from "firebase/auth";

const firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  databaseURL: process.env.REACT_APP_FIREBASE_DATABASE_URL,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
  measurementId: process.env.REACT_APP_FIREBASE_MEASUREMENT_ID,
};

const app = initializeApp(firebaseConfig);
const db = getDatabase(app);
const auth = getAuth(app);

let globalUid = null;
let isInitializing = false; //     

export const initializeAuth = async (userData) => {
  //        
  if (isInitializing || globalUid) {
    console.log("Auth already initialized or in progress");
    return globalUid;
  }

  isInitializing = true; //   

  try {
    let uid;

    if (userData && userData.uuid) {
      // Flutter uuid  
      uid = userData.uuid;
      console.log("=== Flutter  uuid ===");
      console.log("userData:", userData);
      console.log("userData.uuid:", userData.uuid);
      console.log(" uid:", uid);
    } else if (!auth.currentUser) {
      //       
      const userCredential = await signInAnonymously(auth);
      uid = userCredential.user.uid;
      console.log("=== Firebase    uid ===");
      console.log("userCredential:", userCredential);
      console.log("userCredential.user.uid:", userCredential.user.uid);
      console.log(" uid:", uid);
    } else {
      //   
      uid = auth.currentUser.uid;
      console.log("===    ===");
      console.log("auth.currentUser.uid:", uid);
    }

    globalUid = uid;
    console.log("===   globalUid ===");
    console.log("globalUid:", globalUid);

    const userRef = ref(db, `users/${uid}`);
    const snapshot = await get(userRef);

    if (!snapshot.exists()) {
      await set(userRef, {
        cefrLevel: "A1",
        createdAt: Date.now(),
        totalSessions: 0,
        recentScores: [],
      });
    }

    return uid;
  } catch (error) {
    console.error("Error initializing auth:", error);
    throw error;
  } finally {
    isInitializing = false; //   
  }
};

//     
export const getUserInfo = async (uid = null) => {
  try {
    const targetUid = uid || globalUid;

    if (!targetUid) {
      throw new Error("User not initialized");
    }

    console.log("===     ===");
    console.log(" uid:", targetUid);

    const userRef = ref(db, `users/${targetUid}`);
    const snapshot = await get(userRef);

    if (snapshot.exists()) {
      const userData = snapshot.val();
      console.log("  :", userData);
      console.log("  :", {
        cefrLevel: userData.cefrLevel,
        totalSessions: userData.totalSessions,
        recentScores: userData.recentScores,
        createdAt: userData.createdAt,
        chat: userData.chat ? "" : "",
        wrongNote: userData.wrongNote ? "" : "",
      });

      return userData;
    } else {
      console.log("   .  ");
      return {
        cefrLevel: "A1",
        createdAt: Date.now(),
        totalSessions: 0,
        recentScores: [],
      };
    }
  } catch (error) {
    console.error("   :", error);
    return {
      cefrLevel: "A1",
      createdAt: Date.now(),
      totalSessions: 0,
      recentScores: [],
    };
  }
};

//     
export const updateUserInfo = async (updates, uid = null) => {
  try {
    const targetUid = uid || globalUid;

    if (!targetUid) {
      throw new Error("User not initialized");
    }

    console.log("===     ===");
    console.log(" uid:", targetUid);
    console.log(" :", updates);

    const userRef = ref(db, `users/${targetUid}`);

    //   
    const snapshot = await get(userRef);
    const currentData = snapshot.exists() ? snapshot.val() : {};

    //     
    const updatedData = {
      ...currentData,
      ...updates,
      updatedAt: Date.now(),
    };

    await set(userRef, updatedData);

    console.log("   :", updatedData);
    return updatedData;
  } catch (error) {
    console.error("   :", error);
    throw error;
  }
};

