import React from "react";
import CharacterListItem from "./CharacterListItem";
import styles from "../styles/CharacterList.module.css";

function CharacterList({ characters, onSelect, userInfo, globalUid }) {
  const handleCharacterSelect = (character) => {
    console.log("===      ===");
    console.log(" :", character);
    console.log(" :", userInfo);
    console.log("globalUid:", globalUid);
    onSelect(character);
  };

  return (
    <>
      <div className={styles.appbar}>
        <span className={styles.title}>Chat</span>
        {userInfo && (
          <div className={styles.userInfo}>
            <span className={styles.userLevel}>: {userInfo.cefrLevel}</span>
            <span className={styles.sessionCount}>
              : {userInfo.totalSessions || 0}
            </span>
          </div>
        )}
      </div>
      <div className={styles.list}>
        <div className={styles.selectTitle}>  </div>
        {userInfo &&
          userInfo.recentScores &&
          userInfo.recentScores.length > 0 && (
            <div className={styles.recentScore}>
               :{" "}
              {userInfo.recentScores[userInfo.recentScores.length - 1]}
            </div>
          )}
        {characters.map((character, index) => (
          <CharacterListItem
            key={character.id}
            character={character}
            onSelect={handleCharacterSelect}
            className={`${styles.item} ${styles[`delay-${index}`]}`}
          />
        ))}
      </div>
    </>
  );
}

export default CharacterList;
